:root{
     --primary:#ffff;
}

.btn {
    padding: 8px 20px;
    border-radius: 4px;
    outline: none;
    border:none;
    cursor: pointer;
    white-space: nowrap;

}

.btn--primary {
    background-color: var(--primary);
    color: #242424;
    border: 1px solid var(--primary);
}
.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    border: 1px solid var(--primary);
    transition: all 0.3s ease-out;
}
.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}
.btn--large {
    padding: 12px 26px;
    font-size: 20px;
}
.btn--mobile {
    text-align: center;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 24px;
    color: white;
    padding: 14px 20px;
    border: 1px solid #ffff;
    transition:  all 0.3s ease-out;
}
.btn--wide{
    padding: 12px 64px;
    font-size: 20px;
}
.btn--large:hover, .btn--medium:hover, .btn--mobile:hover {
    transition: all 0.3s ease-out;
    background:#ffff;
    color: #242424;
}
.btn--wide:hover{
    color: #ffff;
    background-color: #ffb500;
    transition: all 0.2s ease-out;

}
.blue{
    background-color: #00187e;
    color: #ffff;
    border: none;
}
.red{
    background-color: #f00946;
    color: #ffff;
    border: none;
}
.primary{
    background-color: #242424;
    color: #ffff;
    border: none;
}
.primary:hover{
    background-color: #242424;
    color: #242424;
    border: none;
}
.green{
    background-color: #25ce4a;
    color: #ffff;
    border: none;
}
.green:hover{
    background-color: #242424;
    
}
.btn-link{
 display: flex;
 justify-content: center;
 align-items: center;
 text-decoration: none;
 padding: 8px 16px;
 height: 100%;
 width: 100%;
}